<template>
  <div class="autocomplete">
    <div>
      <span
        class="mdl-chip mdl-chip--deletable"
        v-if="multiSelect && displayChips"
        v-for="item in selectedObjectList"
        :key="item.id"
      >
        <span class="mdl-chip__text">{{item[field]}}</span>
        <button
          type="button"
          class="mdl-chip__action"
          @click="deselect(item)"
        >
          <i class="material-icons">cancel</i>
        </button>
      </span>
    </div>
    <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label">
      <input type="text"
        class="mdl-textfield__input"
        id="autocomplete"
        v-model="search"
        @input="onChange"
        @keydown.down="onKeyDown"
        @keydown.up="onKeyUp"/>
      <label class="mdl-textfield__label" for="autocomplete">{{placeholder}}</label>
      <ul class="autocomplete-results" v-show="isOpen">
        <li
          class="autocomplete-result"
          v-for="(item, i) in data"
          :key="i"
          @click="onClick(item)"
          :class="{ 'active': i === counter }"
        >
          {{item[field]}}
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
let debounce = require('lodash.debounce')
export default {
  name: 'autocomplete',
  props: {
    data: {
      type: Array,
      default: []
    },
    field: {
      type: String,
      required: true
    },
    action: {
      type: Function,
      required: true
    },
    multiSelect: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    displayChips: {
      type: Boolean,
      default: true
    },
    emitObjects: {
      type: Boolean,
      default: false
    }
  },
  mounted () {
    window.componentHandler.upgradeAllRegistered()
  },
  data () {
    return {
      search: '',
      isOpen: false,
      counter: 0,
      selected: {},
      selectedObjectList: [],
      selectedIdList: []
    }
  },
  methods: {
    onChange () {
      if (this.search.length > 3) {
        try {
          debounce(
            this.fetchData(),
            500
          )
        } catch (err) {
          return err
        }
      }
    },
    fetchData () {
      this.action({
        search: this.search,
        paze_size: 25
      }).then(() => {
        this.isOpen = true
      })
    },
    onKeyDown () {
      if (this.counter < this.data.length) {
        this.counter = this.counter + 1
        this.selected = this.data[this.counter]
      }
    },
    onKeyUp () {
      if (this.counter > 0) {
        this.counter = this.counter - 1
        this.selected = this.data[this.counter]
      }
    },
    onEnter () {
      this.onSelect()
    },
    onClick (item) {
      this.selected = item
      this.onSelect()
    },
    emitValue () {
      if (this.multiSelect && this.emitObjects) {
        this.$emit('selected', this.selectedObjectList)
      } else if (this.multiSelect) {
        this.$emit('selected', this.selectedIdList)
      } else {
        this.$emit('selected', this.selected)
      }
    },
    onSelect () {
      this.counter = 0
      this.isOpen = false
      this.search = ''
      if (this.multiSelect) {
        if (this.selectedIdList.indexOf(this.selected.id) === -1) {
          this.selectedObjectList.push(this.selected)
          this.selectedIdList.push(this.selected.id)
        }
      }
      this.emitValue()
    },
    deselect (removeItem) {
      let items = []
      let ids = []
      this.selectedObjectList.forEach(item => {
        if (item.id !== removeItem.id) {
          items.push(item)
          ids.push(item.id)
        }
      })
      this.$set(this, 'selectedObjectList', items)
      this.$set(this, 'selectedIdList', ids)
      this.emitValue()
    }
  }
}
</script>





